  
  .review-header {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .review-form {
    display: flex;
    flex-direction: column;
  }
  
  .review-textarea {
    margin-bottom: 10px;
    height: 40px; /* Reduced height */
    resize: none;
  }
  
  .review-submit {
    background-color: #4caf50;
    color: white;
    width: 200px;
    padding: 8px; /* Reduced padding */
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .review-submit:hover {
    background-color: #45a049;
  }